<template>
  <div class="showpage">
    <div>
      <div class="top">
        <span class="jump fl" @click=(jump())></span>
        <p class="tit onetext fl">{{datas.data.name}}</p>
      </div>
      <div class="content">
        <div class="lovebook">
          <div class="fl imgs">
            <img :src="datas.data.cover" alt="暂无图片" />
          </div>
          <div class="fl books clearfixed">
            <span class="titlt onetext">{{datas.data.name}}</span>
            <div class="at clearfixed">
              <span class="author onetext fl">{{datas.data.author}}</span>
              <div class="tag onetext fl">
                <span>{{datas.data.tag}}</span>
              </div>
            </div>
            <span class="pop">{{datas.data.fans}}万人气</span>
            <span class="article onetext">更新：{{datas.data.time}}</span>
          </div>
        </div>
        <div class="btn">
          <span class="fl" @click="collect">追书</span>
          <span class="fr" @click="goread({name:'read',query:{dasauthor:datas.data.author,datname:datas.data.name}})">开始阅读</span>
        </div>
        <div class="new">
          <span class="news fl">最新</span>
          <span class="art onetext fl">{{datas.data.num}}</span>
          <span class="load fr">连载中</span>
        </div>
        <div class="all">
          <span class="catalog fl">目录</span>
          <span class="chapter fl">共{{datas.data.aritcle}}章</span>
          <span class="load fr"></span>
        </div>
        <div class="introduction">
          <h1>简介</h1>
          <div class="word">{{datas.data.introduce}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const API_PROXY = "https://bird.ioliu.cn/v1/?url=";
export default {
  name: "showpage",

  data() {
    return {
      datas: {
        data: {},
        list:{},
        chapter:{}
      }
    };
  },
  methods:{
      //跳回上一个历史页面
      jump(){
          this.$router.go(-1)
      },
      //开始阅读
      goread(o){
        //路由跳转
          this.$router.push(o)

          let self = this

          //获取本地数据
          let lists = localStorage.getItem('lists');

          lists = lists ? JSON.parse(lists) : []

          //判断数据是否存在本地里
          for(let i = 0; i< lists.length; i++){
            if(lists[i].datas.data.author == self.datas.data.author && lists[i].data.name == self.datas.data.name){
              // console.log('已有数据')
              return
            }
          }
          let time = new Date().getTime();

          let allists = {
            datas:self.datas,
            time,
          }

          lists.push(allists)

          localStorage.setItem('lists',JSON.stringify(lists))
      },
      //收藏书本
      collect(){

        let self = this;

        //获取本地数据
        let bookslists = localStorage.getItem('bookslists');

        bookslists = bookslists ? JSON.parse(bookslists) : []
        // console.log("bookslists==>",bookslists)
        // console.log("self.datas==>",self.datas)

        //判断数据是否存在本地里
        for(let i = 0; i< bookslists.length; i++){
            if(bookslists[i].chapteraut == self.datas.data.author && bookslists[i].chapterbokname == self.datas.data.name){
              // console.log('数据重复，已有数据，拦截')
              return
            }
          }

          let bokshelfs = {
            chapteraut:self.datas.data.author,
            chapterpic:self.datas.data.cover,
            chapternum:self.datas.list[0].num,
            chapterbokname:self.datas.data.name
          }

          bookslists.push(bokshelfs)

          localStorage.setItem('bookslists',JSON.stringify(bookslists))

      }
  },

  created() {
    //获取searchs页面传过来的url
    let arr = this.$route.query;
    // console.log("arr==>",arr)
    this.geturl = arr.seaurl;
    // console.log("this.geturl==>",this.geturl)

    //按获取的url获取详情
    this.axios
      .get(API_PROXY + "http://api.pingcc.cn/?xsurl1=" + this.geturl)
      .then(result => {
        // console.log("result ==> ", result);
        this.datas = result.data;

        // console.log("this.datas ==> ", this.datas);
        //获取当前更新到哪章节
        this.datas.data.aritcle = result.data.list.length - 1;
        // 获取随机章节
        this.datas.data.num = arr.num;
        //生成随机粉丝
        let ran = Math.random();
        this.datas.data.fans = parseInt(100000 * ran);
        //生成随机读者留存率
        this.datas.data.save = (100 * ran).toFixed(2);
        //获取tag标题
        this.datas.data.tag = arr.tag;
      })
      .catch(err => {
        // console.log("err ==> ", err);
      });
  }
};
</script>

<style lang="less" scoped>
.showpage {
  .top {
    position: sticky;
    top: 0;
    height: 40px;
    background: #3b454d;
    .jump{
        width: 24px;
        height: 40px;
        background: url("../assets/return.png") no-repeat center left;
        background-size: 18px;  
        margin-left: 10px;
    }
    .tit {
      margin: 0;
      width: calc(100% - 50px);
      height: 40px;
      line-height: 40px;
      color: #fff;
      font-size: 18px;
      text-align: center;
    }
  }
  .content {
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f2f2f2;
    padding: 10px 10px 0;
    .lovebook {
      height: 3.5rem;
      overflow: hidden;
      background-color: #dbdbdb;
      .imgs {
        width: 100px;
        line-height: 3.5rem;
        > img {
          width: 100%;
        }
      }
      .books {
        width: calc(100% - 2.85rem - 40px);
        margin-left: 20px;
        padding-right: 20px;
        margin-top: 20px;
        .titlt {
          display: block;
          font-size: 14px;
          font-weight: bold;
          margin-bottom: 5px;
        }
        .at {
            .author {
                font-size: 12px;
                color: #645b5b;
                font-weight: 600;
                margin-right: 10px;
            }
            .tag {
                display: inline-block;
                margin-bottom: 6px;
                > span {
                padding: 1px 5px;
                border-radius: 20px;
                color: #f3e5e5;
                background-color: #b2b2b2;
                font-size: 10px;
              }
          }
        }
        .pop {
          display: block;
          color: #645b5b;
          margin-bottom: 5px;
        }
        .article {
          display: block;
          color: #645b5b;
          font-size: 13px;
        }
      }
    }
    .btn {
      padding: 15px 30px;
      height: 35px;
      border-bottom: 1px solid #dbd7d7;
      color: #494747;
      font-weight: 600;
      > span {
        width: 120px;
        line-height: 35px;
        border-radius: 6px;
        text-align: center;
        background-color: #dbdbdb;
        font-size: 16px;
        &:active{
            box-shadow: 0 0 3px 4px #ccc4c4 inset;
        }
      }
    }
    .new {
      height: 50px;
      border-bottom: 1px solid #dbd7d7;
      .news {
        display: inline-block;
        padding-left: 26px;
        background: url("../assets/new.png") no-repeat bottom left;
        background-size: 18px;
        height: 28px;
        line-height: 30px;
        font-size: 20px;
        margin-top: 8px;
      }
      .art {
        margin-left: 20px;
        width: 200px;
        color: #a39d9d;
        margin-top: 16px;
        font-size: 13px;
      }
      .load {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        font-size: 12px;
        padding-right: 20px;
        margin-top: 10px;
        color: #dd9090;
        background: url("../assets/jump.png") no-repeat center right;
        background-size: 20px;
      }
    }
    .all {
      height: 50px;
      border-bottom: 1px solid #dbd7d7;
      .catalog {
        display: inline-block;
        padding-left: 26px;
        background: url("../assets/catalog.png") no-repeat bottom left;
        background-size: 20px;
        height: 28px;
        line-height: 30px;
        font-size: 20px;
        margin-top: 8px;
      }
      .chapter {
        margin-left: 20px;
        width: 160px;
        color: #a39d9d;
        margin-top: 16px;
        font-size: 13px;
      }
      .load {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        padding-right: 20px;
        margin-top: 10px;
        background: url("../assets/jump.png") no-repeat center right;
        background-size: 20px;
      }
    }
    .introduction {
      .word {
        padding: 0 20px;
        color: #837f7f;
        font-size: 15px;
        line-height: 20px;
        height: 120px;
        text-indent: 28px;
        overflow: hidden;
        text-align: justify;
        display: -webkit-box;
        -webkit-line-clamp: 6;
        -webkit-box-orient: vertical;
      }
    }
  }
}
</style>